body {
  margin: 0;
  line-height: normal;
}

.popup-overlay {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

:root {
  /* fonts */
  --text-small-link: Roboto;

  /* font sizes */
  --text-small-link-size: 0.88rem;
  --text-regular-semi-bold-size: 1rem;
  --text-medium-normal-size: 1.13rem;
  --heading-h5-size: 1.5rem;
  --heading-h2-size: 3rem;
  --heading-h3-size: 2.5rem;
  --heading-h1-size: 3.5rem;

  /* Colors */
  --white: #fff;
  --color-whitesmoke: #f6f1f0;
  --black: #000;
  --color-sienna-100: #8d5543;
  --gray-2: #4f4f4f;
  --gray-1: #333;

  /* Gaps */
  --gap-61xl: 5rem;
  --gap-5xl: 1.5rem;
  --gap-base: 1rem;
  --gap-3xs: 0.63rem;
  --gap-78xl: 6.06rem;
  --gap-13xl: 2rem;
  --gap-5xs: 0.5rem;

  /* Paddings */
  --padding-45xl: 4rem;
  --padding-xl: 1.25rem;
  --padding-7xl: 1.63rem;
  --padding-31xl: 3.13rem;
  --padding-27xl: 2.88rem;
  --padding-81xl: 6.25rem;
  --padding-3xs: 0.63rem;
  --padding-6xl: 1.56rem;
  --padding-base: 1rem;
  --padding-5xs: 0.5rem;
}
